import axios from 'axios'
import router from '@/router'

export default ({
	namespaced: true,
  state: {
  	authenticated: false,
  	user: null,
    icdx: null
  },
  getters: {
  	authenticated(state){
  		return state.authenticated;
  	},
  	user(state){
  		return state.user;
  	},
    icdx(state){
      return state.icdx;
    }
  },
  mutations: {
    SET_AUTHENTICATED(state, value){ state.authenticated = value },
    SET_USER(state, value) { state.user = value },
    SET_ICDX(state, value) { state.icdx = value }
  },
  actions: {
    async login ({dispatch}, credentials){
      await axios.get('sanctum/csrf-cookie')
      await axios.post('login', credentials)
      dispatch('me')
      router.push('/dashboard')
    },

    async logout({ commit }){
      await axios.post('logout')

      commit("SET_AUTHENTICATED", false)
      commit("SET_USER", null)
    },

    async me({commit}){
      try{
        let response = await axios.get('api/me')
        commit("SET_AUTHENTICATED", true)
        commit("SET_USER", response.data)
      } catch {
        commit("SET_AUTHENTICATED", false)
        commit("SET_USER", null)
      }
    },

    async getIcdx({commit}){
      try{
        let response = await axios.get('api/icdx/get')
        commit("SET_ICDX", response.data)
      } catch {
        commit("SET_USER", null)
      }


    }

  },
 
})
